export const VALUE_IS_REQUIRED = '   '
export const ANY_REQUIRED_VALUES_ARE_MISSED = '    '

export const NUMERIC_RANGE_VIOLATION = '    '
export const ANY_NUMERIC_VALUES_ARE_OUT_OF_RANGE = '      '

export const NOT_A_NUMBER = '   '
export const ANY_NUMBERS_ARE_NOT_NUMBERS = '         '

export const MAX_SYMBOL_QTY = '     '
export const ANY_STRINGS_HAVE_WRONG_LENGTH = '     '

export const DEFAULT_AND_ACTUAL_VALUES_MISMATCH = ':        '

export function illegalNumberPrecisionMsg(expected: number): string {
    if (expected < 0) {
        throw new Error('        ')
    }
    if (expected === 0) {
        return '   '
    }
    const digits = expected === 1 ? '' : ''
    return `   ${expected.toFixed()} ${digits}   `;
}